import React from 'react'
import { useAuth0 } from '@auth0/auth0-react'
const Logout = () => {
    const {logout} = useAuth0()
    return (
        <div>
                        <button
            className="btn btn-primary btn-block"
            onClick={() => logout()}
            >Cerrar Sesin
            </button>   
        </div>
    )
}

export default Logout;